<template lang="pug">
p-slide
  template(#header)
    h2.my-0 {{portfolio.title}}
  info-section(v-if="portfolio.web.list" :name="portfolio.web.title" icon="fas fa-globe")
    .d-flex.flex-wrap.flex-column
      b-col(cols="12" v-for="w in portfolio.web.list").px-1.px-md-2.mb-3
        b-card(no-body).projCard
          b-row(no-gutters)
            b-col(md="4").p-0.projImg
                .img(:style="`background-image: url(${$root.publicPath}images/${w.imgSrc})`")
                //img(:src="`${$root.publicPath}images/${w.imgSrc}`" 
                  img-alt="Project image")
            b-col(md)
              b-card-body(:title="w.title") 
                b-card-text {{w.desc}}
                b-button(:href="w.link" target="_blank").theme-bg.go-to-btn Go to
  info-section(v-if="portfolio.games.list" :name="portfolio.games.title" icon="fas fa-gamepad")
    .d-flex.flex-wrap.flex-column
      b-col(cols="12" v-for="g in portfolio.games.list").px-1.px-md-2.mb-3
        b-card(no-body).projCard
          b-row(no-gutters)
            b-col(md="4").p-0.projImg
              .img(:style="`background-image: url(${$root.publicPath}images/${g.imgSrc})`")
              //img(:src="`${$root.publicPath}images/${g.imgSrc}`" 
                  img-alt="Project image")
            b-col(md)
              b-card-body(:title="g.title")
                b-card-text {{g.desc}}
                b-button(:href="g.link" target="_blank").theme-bg.go-to-btn Go to
      
</template>
<script>
import portfolio from '@/assets/portfolio.json';
import PSlide from '@/components/Slide.vue';
import InfoSection from '@/components/InfoSection.vue';
export default {
    data(){
      return{
        portfolio: portfolio
      }
    },
    components:{
      PSlide,
      InfoSection,
    }
}
</script>
<style lang="scss">
@import '~bootstrap';
.projCard{
  //max-height: 300px;
  .projImg{
    overflow: hidden;
    text-align: center;
    min-height: 200px;
    
    >.img{
      width: 100%;
      height: 100% !important;
      background: {
        position: center;
        size: cover;
        repeat: no-repeat;
      }
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
      max-height: 300px;
    }
  }
  
@include media-breakpoint-down(sm) { 
  .projImg{
    min-height: 300px;
  }
}
  .go-to-btn{
    border: none;
    transition-delay: 0;
    transition: all 0.3s ease-out;
    &:hover{
      box-shadow: 0 0 5px #666;
    }
  }
}


</style>